import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        isLogined: true,//默认是未登录(false) 登录(true)
        isShowMore: false, //actionsheet默认不显示
        unLoginedMoreMenus: {
            login: '登录'
        },
        LoginedMoreMenus: {
            center: '个人中心',
            msg: '消息通知',
            logout: '登出'
        },
        topics: [
            { name: 'all', desc: '全部' },
            { name: 'good', desc: '精华' },
            { name: 'share', desc: '分享' },
            { name: 'ask', desc: '问答' },
            { name: 'job', desc: '招聘' },
        ]
    },
    getters: {
        moreMenus:function(state){  
            // 如果用户是登录状态，显示个人中心 如果是未登录，显示登录
            if(state.isLogined){
              return state.LoginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
          }
    },
    mutations: {
        mutationIsShowMore:function(state,payload){ 
            // state表示当前store对象的state数据，payload表示函数调用时传入的值
            state.isShowMore=payload;
        }
    },
    actions: {},
})
export default store;


