
import {mapState,mapGetters} from 'vuex'; 
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
components: {
ButtonTab,
ButtonTabItem,
XHeader,
Actionsheet
},
computed:{
...mapGetters(['moreMenus']),
...mapState(['topics','isLogined','isShowMore','unLoginedMoreMenus','LoginedMoreMenus']),
isShowMore:{
get:function(){return this.$store.state.isShowMore},
set:function(nval){this.$store.commit('mutationIsShowMore',nval)}
}
},
name: 'app',
methods:{
handleClickMoreMenu(menuKey, menuItem){
console.log(menuKey)
console.log(menuItem)
this.$router.push('/'+menuKey);
},
routeto:function(index){

console.log(index);
this.$router.push('/topiclist/'+this.topics[index].name);
},
showMoreMenus(){  //xheader右侧点击后，显示actionsheet

this.$store.commit('mutationIsShowMore',true);
}
},
data:function(){
return{

}
},
}



