
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name: "topiclist",
props: ["name"],
components: {
Cell,
Group,
Scroller
},
data: function () {
return {
lists: [],
page:1,
limit:10,
pullupConfig:{
content:'上拉加载更多',
downContent:'松开进行加载',
upContent:'上拉加载更多',
loadingContent:'加载中...'
}
};
},



beforeRouteEnter: function (to, from, next) {




next(function (vm) {

vm.$http
.get("/topics", {
params: {
tab: to.params.name,
page:vm.page,
limit:vm.limit 
},
})
.then(function (res) {

if (res.data.success) {
vm.lists = res.data.data;/* [0] */
}
});
});
},
beforeRouteUpdate: function (to, from, next) {

var app = this;
app.$http
.get("/topics", {
params: {
tab: to.params.name,
page:this.page,
limit:this.limit
},
})
.then(function (res) {
if (res.data.success) {
app.lists = res.data.data;/* [0] */
}
});
next();
},
};



